html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden; /* Prevent horizontal scrolling */
  }
  
  .tv-show-detail {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 0 5%;
    background-color: #0d0d1a; /* Fallback background */
  }
  
  /* Background: Image */
  .tv-show-detail-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    overflow: hidden;
  }
  
  .tv-show-detail-background img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
  }
  
  /* Content Section */
  .tv-show-detail-content {
    position: relative;
    z-index: 1;
    max-width: 800px;
    padding-top: 5rem; /* Offset for readability */
  }
  
  .tv-show-detail-title {
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 1rem;
  }
  
  .tv-show-detail-overview {
    font-size: 1.2rem;
    line-height: 1.6;
    margin-bottom: 1.5rem;
    color: rgba(255, 255, 255, 0.85);
  }
  
  /* Show Info Section */
  .tv-show-detail-info {
    display: flex;
    align-items: center;
    margin-bottom: 2rem;
    gap: 1.5rem;
  }
  
  .tv-show-detail-rating {
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .tv-show-detail-meta {
    display: flex;
    gap: 1.5rem;
  }
  
  .meta-item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-size: 0.9rem;
    color: rgba(255, 255, 255, 0.8);
  }
  
  /* Call to Action Button */
  .tv-show-detail-cta {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
    font-weight: bold;
    background-color: #ffd700;
    color: #0d0d1a;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .tv-show-detail-cta:hover {
    background-color: #ffcc33;
  }
  
  .related-tv-shows {
    position: fixed;
    bottom: 1rem; /* Position at the bottom */
    left: 2rem; /* Position towards the left */
    display: flex;
    flex-direction: row; /* Arrange items horizontally */
    gap: 1rem; /* Space between related TV show cards */
    background: rgba(0, 0, 0, 0.7); /* Add background for visibility */
    padding: 1rem;
    border-radius: 8px;
  }
  
  .related-tv-show-card {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    overflow: hidden;
    width: 150px; /* Set a fixed width */
  }
  
  .related-tv-show-card img {
    width: 100%;
    height: 100px; /* Adjust height */
    object-fit: cover;
  }
  
  .related-tv-show-content {
    padding: 0.5rem;
  }
  
  .related-tv-show-title {
    margin: 0;
    font-size: 0.9rem;
    color: #fff;
  }
  
  .related-tv-show-subtitle {
    margin: 0.2rem 0 0 0;
    font-size: 0.8rem;
    color: rgba(255, 255, 255, 0.8);
  }
  
  /* Pagination Dots */
  .pagination-dots {
    display: flex;
    gap: 0.5rem;
    justify-content: center;
    margin-top: 2rem;
  }
  
  .dot {
    width: 8px;
    height: 8px;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 50%;
  }
  
  .dot.active {
    background: #FFA500;
  }
  
  /* Trailer Popup */
  .trailer-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
  }
  
  .trailer-popup-content {
    position: relative;
    width: 80%;
    max-width: 800px;
    height: 0;
    padding-top: 56.25%; /* Maintain 16:9 aspect ratio */
    background-color: #000;
    border-radius: 8px;
    overflow: hidden;
  }
  
  .trailer-close-btn {
    position: absolute;
    top: 15px;
    right: 15px;
    background: rgba(0, 0, 0, 0.6);
    border: none;
    color: white;
    font-size: 24px;
    cursor: pointer;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background 0.3s ease;
  }
  
  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  